import { CommonModule } from '@angular/common';
import { HttpClientModule } from '@angular/common/http';
import { NgModule, Optional, SkipSelf } from '@angular/core';
import { MatIconRegistry } from '@angular/material/icon';
import { DomSanitizer } from '@angular/platform-browser';
import { IconsRegistry } from './icons-registry';

@NgModule({
  declarations: [],
  imports: [CommonModule, HttpClientModule],
  providers: [],
})
export class CoreModule {
  constructor(@Optional() @SkipSelf() parentModule: CoreModule, matIconRegistry: MatIconRegistry, sanitizer: DomSanitizer) {
    this.throwIfAlreadyLoaded(parentModule, 'CoreModule');
    IconsRegistry.registerIcons(matIconRegistry, sanitizer);
  }

  // tslint:disable-next-line:no-any
  private throwIfAlreadyLoaded(parentModule: any, moduleName: string) {
    if (parentModule) {
      throw new Error(`${moduleName} has already been loaded. Import Core modules in the AppModule only.`);
    }
  }
}
